@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
body {
  margin: 0;
  background-color: rgb(231, 231, 231);
  /*
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  */
}
/*
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
*/
:root {
  --txt-color: #757575;
  --error-color: #900d0d;
  --red-color: #cf1b1b;
  --green-color: #438a5e;
  --delete-color: black; /* cambiar de color para el not done  */
  --main-font: 'Poppins', sans-serif;

  /* shadows colors */
  --black-shadow: rgba(0, 0, 0, 0.185);
  --white-shadow: rgba(255, 255, 255, 0.3);
  --white2-shadow: rgba(255, 255, 255, 0.637);
}
.position {
  padding-top: 100px;
}

  .alert {
    width: 100%;
    padding-top: 10px;
    padding-bottom: 10px;

    box-shadow: 2px 6px 10px 0 var(--black-shadow),
      -2px -5px 15px 0 var(--white2-shadow);
  }

  .alert p {
    color: var(--txt-color);
    font-family: var(--main-font);
    font-size: 1.5em;
    text-align: center;

    -webkit-user-select: none;

       -moz-user-select: none;

        -ms-user-select: none;

            user-select: none;
  }
  .alert span {
    color: var(--error-color);
  }
    .contact {
      width: -webkit-fit-content;
      width: -moz-fit-content;
      width: fit-content;    
      margin: auto;
    }
      .contact-t, .contact-g {
        font-size: 4em;
      }
      .contact-t { 
        color: #1da1f2;
        margin-right: 2em;
      }
      .contact-g { color: #24292e; }
      .contact-t:hover, .contact-g:hover {
        cursor: pointer;
      }
@-webkit-keyframes Addbutton {
  from {
    box-shadow: 5px 5px 10px 0 var(--black-shadow),
      -3px -3px 10px 0 var(--white-shadow);
  }
  to {
    box-shadow: inset 5px 5px 10px 0 var(--black-shadow),
      inset -3px -3px 10px 0 var(--white-shadow);
  }
}

@keyframes Addbutton {
  from {
    box-shadow: 5px 5px 10px 0 var(--black-shadow),
      -3px -3px 10px 0 var(--white-shadow);
  }
  to {
    box-shadow: inset 5px 5px 10px 0 var(--black-shadow),
      inset -3px -3px 10px 0 var(--white-shadow);
  }
}

.container {
  width: 100%;
  padding-top: 50px;

  display: flex;

  align-items: center;
}

  .addTask, .addToList {
    height: 40px;

    border: none;
    border-radius: 50px;

    outline: none;

    /* text */
    font-size: 20px;
    font-weight: 500;
    color: var(--txt-color)
  }

  /* text input */
  .addTask {
    width: 60%;
    margin-left: 10%;
    padding-left: 3%;
    padding-right: 3%;

    box-shadow: inset 2px 6px 10px 0 var(--black-shadow),
      inset -2px -5px 15px 0 var(--white-shadow);
    
    background-color: transparent;

    font-family: var(--main-font);
  }
  /* When the text is underline/selected */
  .addTask::selection {
    background-color: rgb(134, 134, 134);
    color: rgb(207, 207, 207);
  }

  /* Add button */
  .addToList {
    width: 10%;

    padding-left: 10px;
    padding-top: 5px;
    margin-left: 10%;

    box-shadow: 5px 5px 10px 0 var(--black-shadow),
      -3px -3px 10px 0 var(--white-shadow);

    background-color: transparent;
  }
  .addToList:hover {
    cursor: pointer;
  }
  .addToList:active {
    box-shadow: inset 5px 5px 10px 0 var(--black-shadow),
      inset -3px -3px 10px 0 var(--white-shadow);

    transition: 1.5s;
  }
    .addToList:active .addIcon {
      margin-top: 8px;
    }


/* Mobile */
@media only screen and (max-width: 768px) {
  .AddToList {
    width: 20%;
    margin-right: 10%;
  }
}

/* Styles for the container of the tasks */
.tasks-container { 
  height: 60%; 
  margin-top: 10px;
  overflow: auto;

  /*display: flex;
  flex-direction: column;*/
}
  /* width */
  ::-webkit-scrollbar {
    width: 10px;
  }
  /* Track */
  ::-webkit-scrollbar-track {
    border-radius: 50px;
    box-shadow: inset 5px 5px 10px 0 var(--black-shadow),
      inset -3px -3px 10px 0 var(--white-shadow);
    background: transparent;
  }
  /* Handle */
  ::-webkit-scrollbar-thumb {
    border-radius: 50px;
    background: rgb(231, 231, 231); 
  }
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #dedede;
  }

/* Styles for the card for the task */
.card-container {
  width: 60%;

  margin: auto;
  margin-bottom: 1em;
  padding-top: 10px;
  padding-bottom: 10px;

  display: flex;

  border-radius: 50px;
  box-shadow: 12px 12px 16px 0 var(--black-shadow),
    -8px -8px 12px 0 var(--white-shadow);
  
  background-color: transparent;
}
  .done, .delete, .notDone, .card-container p {
    color: var(--txt-color);
    font-family: var(--main-font);
  }
  .task-place {
    width: 83%;

    overflow-wrap: break-word;
  }
  .card-container p {
    margin: 0;

    padding-left: 1em;
    padding-right: 0.2em;

    font-size: 20px;
  }
  p::selection {
    background-color: rgb(134, 134, 134);
    color: rgb(207, 207, 207);
  }
  .buttons {
    display: inherit;
    align-self: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
  .done, .delete, .notDone { 
    font-size: 30px;
    fill: #b3b3b3;
  }
  .delete { margin-right: 20px; }
  .done, .notDone { margin-right: 5px; }
  .delete:hover, .done:hover, .notDone:hover { cursor: pointer; }
  .delete:hover { fill: var(--delete-color); }
  .done:hover { fill: var(--green-color); }
  .notDone:hover { fill: var(--red-color); }

  /* To show that the task is done or not */
  .notDoneTask, .doneTask { font-size: 30px; margin-right: 5px; }
  .notDoneTask { fill: var(--red-color); }
  .doneTask { fill: var(--green-color); }

@media only screen and (max-width: 768px) {
  .tasks-container {
    height: 60%;
    margin-top: 20px;
  }
  .card-container {
    width: 80%;
  }
    .task-place {
      width: 100%;
    }
}
@media only screen and (max-width: 320px) {
  .task-place {
    width: 50%;
  }
}

.container-buttons {
  display: flex;

  margin-top: 20px;
}
  .txt {
    margin: 0;
    margin-top: 5px;

    color: var(--txt-color);
    font-family: var(--main-font);
    text-align: center;
  }

  .button-done, .button-notDone {
    width: 100px;
    height: 100px;

    margin: auto;

    border-radius: 50px;
    border-style: none;
  
    outline: none;

    background-color: transparent;
  }

  .button-notDone:hover, .button-done:hover {
    cursor: pointer;
  }

  .button-done:hover p { color: var(--green-color); }
  .button-done:hover .done { fill: var(--green-color); }
  .button-notDone:hover p { color: var(--red-color); }
  .button-notDone:hover .notDone { fill: var(--red-color); }

  /*
    When action (pressed, clicked) to .button-done or .button-notDone
    in CSS is going to make the button with the shadow inset, the color
    and fill is going to be its color. And it is going to show all the
    tasks that has been done or NotDoned.

    When the action is done by a second time, the buttons comes to the
    initial state. It returns to the place where are the tasks
  */


  .shadowOut {
    box-shadow: 12px 12px 16px 0 var(--black-shadow),
      -8px -8px 12px 0 var(--white-shadow);
  }
  .shadowIn {
    box-shadow: inset 5px 5px 10px 0 var(--black-shadow),
      inset -3px -3px 10px 0 var(--white-shadow);
  }

  .sizeIcons {
    font-size: 30px;
  }
  .donePress {
    color: var(--green-color);
  }
  .delPress {
    color: var(--red-color);
  }

  .option {
    margin: 0;
  }

.footer {
  margin-top: 10%;
  padding-bottom: 5px;
  color: #c5c5c5;
  font-family: 'Mansalva', cursive;
  font-size: 1.05em;
  text-align: center;
}
  .name {
    margin-bottom: 5px;
  }
  .contact-t, .contact-g {
    font-size: 2em;
  }
  .contact-t {
    color: #50baed;
    margin-right: 1em;
  }
  .contact-g {
    color: #24292e;
  }

.center {
  width: 60%;
  height: 90%;

  margin: auto;
  margin-top: 40px;

  box-shadow: 12px 12px 16px 0 var(--black-shadow),
    -8px -8px 12px 0 var(--white-shadow);
  border-radius: 50px;

  background-color: transparent;
}
  hr {
    color: var(--txt-color);
    margin-top: 20px;

    width: 70%;
  }

/* For Mobiles */
@media only screen and (max-width: 768px) {
  .center {
    width: 100vw;
    height: 95%;

    box-shadow: none;

    margin-top: 0;
    border-radius: 0;
  }
}

